import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import App from './App'
import router from './router'
import store from './store/'
import axios from 'axios'

Vue.config.productionTip = false
Vue.use(ElementUI)
// /* eslint-disable no-new */
//

const whiteList=['/main','/login','/register','/displayvideo']
router.beforeEach(function(to,from,next){   // 
if(!store.state.login){     //
  
  if((whiteList.indexOf(to.path)!==-1)||(from.path.indexOf('/displayvideo'))!=-1||(to.path.indexOf('/subvideo')!==-1)||(to.path.indexOf('/playvideo')!=-1)||(to.path.indexOf('/viewMobile')!=-1)
  ||(to.path.indexOf('/liveMobile')!=-1)){ 
    //  whiteList :=(
     next()
  }
  else if(sessionStorage.getItem('userName')){         //...; localStorage
    next();
  }
  else{
    next('/login')
  }
}
else{          //
  next()
}
})

new Vue({
  el: '#app',
  router,
  store,
  axios,
  render: h => h(App)
})



